"use server";

import { auth } from "@/lib/auth";
import { headers } from "next/headers";
import { getDb } from "@/lib/db";
import { trades } from "@/db/schema";
import { eq, desc, and } from "drizzle-orm";
import { revalidatePath } from "next/cache";

// Helper to get authenticated user
async function getUser() {
    const session = await auth.api.getSession({
        headers: await headers()
    });
    if (!session?.user) {
        throw new Error("Unauthorized");
    }
    return session.user;
}

export async function getTrades() {
    const user = await getUser();
    const db = getDb();

    // Fetch trades for this user, ordered by date descending
    const data = await db.select()
        .from(trades)
        .where(eq(trades.userId, user.id))
        .orderBy(desc(trades.date));

    return data;
}

export async function createTrade(data: Omit<typeof trades.$inferInsert, "userId">) {
    const user = await getUser();
    const db = getDb();

    // Force userId to match session
    const newTrade = {
        ...data,
        userId: user.id,
        createdAt: new Date(),
        updatedAt: new Date()
    };

    await db.insert(trades).values(newTrade);
    revalidatePath("/trades");
    revalidatePath("/");
    revalidatePath("/reports");
    return { success: true };
}

export async function updateTrade(id: string, data: Partial<typeof trades.$inferInsert>) {
    const user = await getUser();
    const db = getDb();

    await db.update(trades)
        .set({ ...data, updatedAt: new Date() })
        .where(and(eq(trades.id, id), eq(trades.userId, user.id)));

    revalidatePath("/trades");
    revalidatePath("/");
    revalidatePath("/reports");
    return { success: true };
}

export async function deleteTrade(id: string) {
    const user = await getUser();
    const db = getDb();

    await db.delete(trades)
        .where(and(eq(trades.id, id), eq(trades.userId, user.id)));

    revalidatePath("/trades");
    revalidatePath("/");
    revalidatePath("/reports");
    return { success: true };
}
